import React, { Component } from 'react';
import { Card, Col, Row, Switch } from 'antd';
import randomColor from "randomcolor";
import 'antd/dist/antd.css';
import { mean } from "./func-lib/func";
import { ChartDoughnut, ChartRespondenSection, ChartBarLine } from "./chart/chart_view";
import { gender_data, section_data, responden_section, result_mean_data } from "./chart/chart_data";
import {
	section1_data,
	section2_data,
	section3_data,
	section4_data,
	section5_data,
	sections_title
} from "./data/sections";
import {
	firebaseResponden,
	firebaseSection1,
	firebaseSection2,
	firebaseSection3,
	firebaseSection4,
	firebaseSection5
} from "./firebase/firebaseRef";
import RespondenTable from "./component/respondenTable";
import { multiColumnSearchArrayTable } from "array-table-search";
class App extends Component {
	state = {
		card1: false,
		card2: false,
		card3: false,
		card4: false,
		card5: false,
		card6: false,
		card7: false,
		card8: false,
		responden_data: [],
		section1: [],
		section2: [],
		section3: [],
		section4: [],
		section5: [],
		section1Mean: [],
		section2Mean: [],
		section3Mean: [],
		section4Mean: [],
		section5Mean: [ ]
	};
	componentDidMount( ) {
		firebaseResponden.once('value', snap => {
			let tmp = [ ];
			snap.forEach(shot => {
				tmp.push({
					uid: shot.key,
					...shot.val( )
				})
			});
			this.setState({ responden_data: tmp })
		});
		firebaseSection1.once('value', snap => {
			let tmp = [ ];
			snap.forEach(shot => {
				tmp.push([...shot.val( )])
			});
			this.setState({ section1: tmp })
		});
		firebaseSection2.once('value', snap => {
			let tmp = [ ];
			snap.forEach(shot => {
				tmp.push([...shot.val( )])
			});
			this.setState({ section2: tmp })
		});
		firebaseSection3.once('value', snap => {
			let tmp = [ ];
			snap.forEach(shot => {
				tmp.push([...shot.val( )])
			});
			this.setState({ section3: tmp })
		});
		firebaseSection4.once('value', snap => {
			let tmp = [ ];
			snap.forEach(shot => {
				tmp.push([...shot.val( )])
			});
			this.setState({ section4: tmp })
		});
		firebaseSection5.once('value', snap => {
			let tmp = [ ];
			snap.forEach(shot => {
				tmp.push([...shot.val( )])
			});
			this.setState({ section5: tmp })
		})
	}
	handleSwitchCard1 = ( checked ) => {
		this.setState({ card1: checked })
	}
	handleSwitchCard2 = ( checked ) => {
		this.setState({ card2: checked })
	}
	handleSwitchCard3 = ( checked ) => {
		this.setState({ card3: checked })
	}
	handleSwitchCard4 = ( checked ) => {
		this.setState({ card4: checked })
	}
	handleSwitchCard4 = ( checked ) => {
		this.setState({ card4: checked })
	}
	handleSwitchCard5 = ( checked ) => {
		this.setState({ card5: checked })
	}
	handleSwitchCard6 = ( checked ) => {
		this.setState({ card6: checked })
	}
	handleSwitchCard7 = ( checked ) => {
		this.setState({ card7: checked })
	}
	handleSwitchCard8 = ( checked ) => {
		this.setState({ card8: checked })
	}
	render( ) {
		const {
			responden_data,
			section1,
			section2,
			section3,
			section4,
			section5,
			section1Mean,
			section2Mean,
			section3Mean,
			section4Mean,
			section5Mean,
			card1,
			card2,
			card3,
			card4,
			card5,
			card6,
			card7,
			card8
		} = this.state;
		const filterOptionTable = ( gender ) => ({
			gender: {
				type: 'exact',
				value: gender
			}
		});
		const Male = multiColumnSearchArrayTable(responden_data, filterOptionTable( 'male' ));
		const Female = multiColumnSearchArrayTable(responden_data, filterOptionTable( 'female' ));
		const SectionChart = ( section ) => responden_data.map(( data, i ) => {
			return responden_section(data.nama, randomColor({ luminosity: 'dark', format: 'rgba', alpha: 0.7 }), section === 1
				? section1[i]
				: section === 2
					? section2[i]
					: section === 3
						? section3[i]
						: section === 4
							? section4[i]
							: section === 5
								? section5[i]
								: [ ])
		});
	
		const SectionMean = ( ) => responden_data.map(( data, i ) => {
			section1Mean.push(mean(section1[i]));
			section2Mean.push(mean(section2[i]));
			section3Mean.push(mean(section3[i]));
			section4Mean.push(mean(section4[i]));
			section5Mean.push(mean(section5[i]));
			return responden_section(data.nama, randomColor({ luminosity: 'dark', format: 'rgba', alpha: 0.7 }), [
				mean(section1[i]),
				mean(section2[i]),
				mean(section3[i]),
				mean(section4[i]),
				mean(section5[i])
			])
		});
		return (
			<div>
				<div style={{
					background: '#ECECEC',
					padding: '30px'
				}}>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Total Responden" bordered={false}>
								<Col span={12}>
									<div>
										<span style={{
											display: 'flex',
											alignItems: 'center',
											justifyContent: 'center',
											fontSize: '10vh'
										}}>Total :{responden_data.length}</span>
										<span style={{
											display: 'flex',
											alignItems: 'center',
											justifyContent: 'center',
											fontSize: '4vh'
										}}>Laki-laki :{Male.length}</span>
										<span style={{
											display: 'flex',
											alignItems: 'center',
											justifyContent: 'center',
											fontSize: '4vh'
										}}>Perempuan :{Female.length}</span>
									</div>
								</Col>
								<Col span={12}><ChartDoughnut data={gender_data( Male.length, Female.length )}/></Col>
							</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Data Responden" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard1
							} />}>{card1
									? <RespondenTable responden_data={responden_data} section1={section1} section2={section2} section3={section3} section4={section4} section5={section5}/>
									: null}</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Section 1" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard2
							} />}>{card2
									? <ChartRespondenSection title="Section 1" data={section_data(section1_data, responden_data.length !== 0 && section1.length !== 0 && section2.length !== 0 && section3.length !== 0 && section4.length !== 0 && section5.length !== 0
											? SectionChart( 1 )
											: [ ])}/>
									: null}</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Section 2" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard3
							} />}>{card3
									? <ChartRespondenSection title="Section 2" data={section_data(section2_data, responden_data.length !== 0 && section1.length !== 0 && section2.length !== 0 && section3.length !== 0 && section4.length !== 0 && section5.length !== 0
											? SectionChart( 2 )
											: [ ])}/>
									: null}</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Section 3" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard4
							} />}>{card4
									? <ChartRespondenSection title="Section 3" data={section_data(section3_data, responden_data.length !== 0 && section1.length !== 0 && section2.length !== 0 && section3.length !== 0 && section4.length !== 0 && section5.length !== 0
											? SectionChart( 3 )
											: [ ])}/>
									: null}</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Section 4" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard5
							} />}>{card5
									? <ChartRespondenSection title="Section 4" data={section_data(section4_data, responden_data.length !== 0 && section1.length !== 0 && section2.length !== 0 && section3.length !== 0 && section4.length !== 0 && section5.length !== 0
											? SectionChart( 4 )
											: [ ])}/>
									: null}</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Section 5" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard6
							} />}>{card6
									? <ChartRespondenSection title="Section 5" data={section_data(section5_data, responden_data.length !== 0 && section1.length !== 0 && section2.length !== 0 && section3.length !== 0 && section4.length !== 0 && section5.length !== 0
											? SectionChart( 5 )
											: [ ])}/>
									: null}</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Rata-rata" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard7
							} />}>{card7
									? <ChartRespondenSection title="Rata-rata" data={section_data(sections_title, responden_data.length !== 0 && section1.length !== 0 && section2.length !== 0 && section3.length !== 0 && section4.length !== 0 && section5.length !== 0
											? SectionMean( )
											: [ ])}/>
									: null}</Card>
						</Col>
					</Row>
					<Row gutter={16} style={{
						marginBottom: 24
					}}>
						<Col span={24}>
							<Card title="Result" bordered={false} extra={< Switch onChange = {
								this.handleSwitchCard8
							} />}>{card8
									? <ChartBarLine data={result_mean_data([
											section1Mean.length !== 0
												? mean( section1Mean )
												: 0,
											section2Mean.length !== 0
												? mean( section2Mean )
												: 0,
											section3Mean.length !== 0
												? mean( section3Mean )
												: 0,
											section4Mean.length !== 0
												? mean( section4Mean )
												: 0,
											section5Mean.length !== 0
												? mean( section5Mean )
												: 0
										])}/>
									: null}</Card>
						</Col>
					</Row>
				</div>
			</div>
		)
	}
}
export default App;