import React, { Component } from 'react';
import { Table } from 'antd';
import {mean} from "../func-lib/func";
import { section1_data, section2_data, section3_data, section4_data, section5_data } from "../data/sections";
class RespondenTable extends Component {
	
	renderCol = ( section_data, key ) => {
		let tmp_col = [ ];
		section_data.map(( data, i ) => {
			tmp_col.push({
				title: data,
				dataIndex: key + ( i + 1 ),
				key: key + ( i + 1 ),
				width: 100
			})
		});
		return tmp_col
	}
	render( ) {
		const columns = [
			{
				title: 'Name',
				dataIndex: 'name',
				key: 'name',
				width: 170,
				fixed: 'left'
			}, {
				title: 'Responden Data',
				children: [
					{
						title: 'Umur',
						dataIndex: 'age',
						key: 'age',
						width: 60,
						// sorter: ( a, b ) => a.age - b.age
					}, {
						title: 'Email',
						dataIndex: 'email',
						width: 200,
						key: 'email'
					}, {
						title: 'Gender',
						dataIndex: 'gender',
						width: 65,
						key: 'gender'
					}, {
						title: 'Pendidikan',
						dataIndex: 'pendidikan',
						width: 90,
						key: 'pendidikan'
					}, {
						title: 'Submit Time',
						dataIndex: 'time',
						width: 100,
						key: 'time'
					}
				]
			}, {
				title: 'Section 1',
				children: this.renderCol( section1_data, 's1_' )
			}, {
				title: 'Section 2',
				children: this.renderCol( section2_data, 's2_' )
			}, {
				title: 'Section 3',
				children: this.renderCol( section3_data, 's3_' )
			}, {
				title: 'Section 4',
				children: this.renderCol( section4_data, 's4_' )
			}, {
				title: 'Section 5',
				children: this.renderCol( section5_data, 's5_' )
			}, {
				title: 'Rata-rata hasil Section',
				fixed: 'right',
				children: [
					{
						title: '1',
						dataIndex: 'ms1',
						key: 'ms1',
						width: 60,
						sorter: ( a, b ) => a.ms1 - b.ms1
					}, {
						title: '2',
						dataIndex: 'ms2',
						key: 'ms2',
						width: 60,
						sorter: ( a, b ) => a.ms2 - b.ms2
					}, {
						title: '3',
						dataIndex: 'ms3',
						key: 'ms3',
						width: 60,
						sorter: ( a, b ) => a.ms3 - b.ms3
					}, {
						title: '4',
						dataIndex: 'ms4',
						key: 'ms4',
						width: 60,
						sorter: ( a, b ) => a.ms4 - b.ms4
					}, {
						title: '5',
						dataIndex: 'ms5',
						key: 'ms5',
						width: 60,
						sorter: ( a, b ) => a.ms5 - b.ms5
					}
				]
			}
		];
		const {
			responden_data,
			section1,
			section2,
			section3,
			section4,
			section5
		} = this.props;
		const data_res = [ ];
		if ( responden_data.length !== 0 && section1.length !== 0 && section2.length !== 0 && section3.length !== 0 && section4.length !== 0 && section5.length !==0 ) {
			responden_data.map(( data, i ) => {
				data_res.push({
					key: i,
					name: data.nama,
					age: data.umur,
					email: data.email,
					gender: data.gender,
					pendidikan: data.pendidikan,
					time: new Date( data.timestamp ).toLocaleDateString( "en-US" ),
					s1_1: section1[i][0],
					s1_2: section1[i][1],
					s1_3: section1[i][2],
					s1_4: section1[i][3],
					s1_5: section1[i][4],
					s1_6: section1[i][5],
					s1_7: section1[i][6],
					s1_8: section1[i][7],
					s1_9: section1[i][8],
					s1_10: section1[i][9],
					s1_11: section1[i][10],
					s1_12: section1[i][11],
					s2_1: section2[i][0],
					s2_2: section2[i][1],
					s2_3: section2[i][2],
					s2_4: section2[i][3],
					s2_5: section2[i][4],
					s2_6: section2[i][5],
					s2_7: section2[i][6],
					s2_8: section2[i][7],
					s2_9: section2[i][8],
					s2_10: section2[i][9],
					s2_11: section2[i][10],
					s2_12: section2[i][11],
					s3_1: section3[i][0],
					s3_2: section3[i][1],
					s3_3: section3[i][2],
					s3_4: section3[i][3],
					s3_5: section3[i][4],
					s3_6: section3[i][5],
					s3_7: section3[i][6],
					s3_8: section3[i][7],
					s3_9: section3[i][8],
					s3_10: section3[i][9],
					s3_11: section3[i][10],
					s3_12: section3[i][11],
					s4_1: section4[i][0],
					s4_2: section4[i][1],
					s4_3: section4[i][2],
					s4_4: section4[i][3],
					s4_5: section4[i][4],
					s4_6: section4[i][5],
					s4_7: section4[i][6],
					s4_8: section4[i][7],
					s4_9: section4[i][8],
					s4_10: section4[i][9],
					s4_11: section4[i][10],
					s4_12: section4[i][11],
					s5_1: section5[i][0],
					s5_2: section5[i][1],
					s5_3: section5[i][2],
					s5_4: section5[i][3],
					s5_5: section5[i][4],
					s5_6: section5[i][5],
					s5_7: section5[i][6],
					s5_8: section5[i][7],
					s5_9: section5[i][8],
					s5_10: section5[i][9],
					s5_11: section5[i][10],
					s5_12: section5[i][11],
					ms1: mean(section1[i]),
					ms2: mean(section2[i]),
					ms3: mean(section3[i]),
					ms4: mean(section4[i]),
					ms5: mean(section5[i])
				})
			})
		}
		return ( <Table columns={columns} dataSource={data_res} bordered size="middle" scroll={{
			x: 'calc(700px + 50%)',
			y: 480
		}}/> )
	}
}
export default RespondenTable;