import React from 'react';
import { Doughnut, Line, Bar } from 'react-chartjs-2';
export const ChartDoughnut = ( props ) => ( <Doughnut data={props.data} options={{
	legend: {
		position: 'bottom'
	}
}}/> );
export const ChartRespondenSection = ( props ) => ( <Line data={props.data} options={{
	responsive: true,
	legend: {
		position: 'bottom'
	},
	title: {
		display: true,
		text: props.title
	},
	tooltips: {
		mode: 'index',
		intersect: false,
		itemSort: ( a, b, data ) => b.yLabel - a.yLabel
	},
	hover: {
		mode: 'nearest',
		intersect: true
	},
	scales: {
		x: {
			display: true,
			scaleLabel: {
				display: true,
				labelString: 'Month'
			}
		},
		y: {
			display: true,
			scaleLabel: {
				display: true,
				labelString: 'Value'
			}
		}
	}
}}/> );
export const ChartBarLine = ( props ) => ( <Bar data={props.data} options={{
	responsive: true,
	legend: {
		display: false
	},
	tooltips: {
		mode: 'label'
	},
	elements: {
		line: {
			fill: false
		}
	},
	scales: {
		xAxes: [
			{
				display: true,
				gridLines: {
					display: false
				},
				// labels: {
				// 	show: true
				// },
				labels: [ 'Section 1 : '+props.data.datasets[0].data[0], 'Section 2 : '+props.data.datasets[0].data[1], 'Section 3 : '+props.data.datasets[0].data[2], 'Section 4 : '+props.data.datasets[0].data[3], 'Section 5 : '+props.data.datasets[0].data[4] ]
			}
		],
		yAxes: [
			{
				type: 'linear',
				display: true,
				position: 'left',
				id: 'y-axis-1',
				labels: {
					show: true
				}
			}
		]
	}
}}/> );